import React, { useContext, useState } from "react";
import "./Home.css";
import Landing from "./Landing";
import Project from "./Project";
import { _ILLUSTRATION, _WEBDESIGN } from "../Const";
import { HomeText } from "../Data/Text";
import { MinorContext } from "../MinorContext";
import NavBar from "./NavBar";
import Footer from "./Footer";

const Home = props => {
  const { landingOn, setLandingOn, language } = useContext(MinorContext);
  const [lockPage, setLockPage] = useState(landingOn ? "Lock" : "");

  const handleScroll = e => {
    if (landingOn) {
      setLandingOn(false);
      setTimeout(() => {
        setLockPage("");
      }, 400);
    }
  };
  return (
    <div
      className={`Home ${lockPage}`}
      onWheel={handleScroll}
      onTouchStart={handleScroll}
      onClick={handleScroll}
    >
      <NavBar match={props.match} />
      <Landing on={landingOn ? "" : "On"} />
      <div className="container">
        <div className="proyectos">
          <Project
            linkto={`/${_ILLUSTRATION}/`}
            name={HomeText.ilustracion[language]}
            img="https://i.imgur.com/PKkcxfs.jpg"
          >
            {HomeText.ilustracionLabel[language]}
          </Project>
          <Project
            linkto={`/${_WEBDESIGN}/`}
            name={HomeText.diseño[language]}
            img="https://i.imgur.com/MhXIhT7.jpg"
          >
            {HomeText.diseñoLabel[language]}
          </Project>
        </div>
        <div className="HomeInfo">
          <div className="HomeInfoText">
            <p>
              {HomeText.homeInfo1[language]}
              <b>diazjmauricio95@gmail.com</b>
            </p>
            <p>{HomeText.homeInfo2[language]}</p>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default Home;
