import React, { useState, useContext } from "react";
import "./NavBar.css";
import { Link } from "react-router-dom";
import { _ILLUSTRATION, _WEBDESIGN, _ABOUT } from "../Const";
import { NavBarText } from "../Data/Text";
import { MinorContext } from "../MinorContext";

const NavBar = props => {
  const { language } = useContext(MinorContext);
  const links = [
    { name: NavBarText.inicio[language], path: "/" },
    {
      name: NavBarText.ilustracion[language],
      path: `/${_ILLUSTRATION}/`
    },
    { name: NavBarText.diseño[language], path: `/${_WEBDESIGN}/` },
    {
      name: NavBarText.contacto[language],
      path: `/${_ABOUT}/`
    }
  ];
  return (
    <div className="NavBar">
      <div className="NavBarBackground" />
      <div className="container">
        {/* Desktop */}
        <nav className="content desktop">
          {links.map((link, index) => {
            let classN = props.match.path === link.path ? "selected" : "";
            return (
              <Link to={link.path} key={index} className={classN}>
                {link.name}
              </Link>
            );
          })}
          <Language l={language} />
        </nav>
        {/* Mobile */}
        <nav className="content mobile">
          <div className="burger">
            <input type="checkbox" className="mobileBurger" />
            <span />
            <span />
            <span />
            <div className="mobileMenu">
              {links.map((link, index) => {
                let classN = props.match.path === link.path ? "selected" : "";
                return (
                  <Link to={link.path} key={index} className={classN}>
                    {link.name}
                  </Link>
                );
              })}
              <Language l={language} />
            </div>
          </div>
        </nav>
      </div>
    </div>
  );
};

export default NavBar;

//  LANGUAGE
//  ---INITIALSTATE
const LanguageInitialState = l => {
  if (l === 0) {
    return ["selected", ""];
  }
  return ["", "selected"];
};
//  ---COMPONENT
const Language = ({ l }) => {
  const [langClass, setLangClass] = useState(LanguageInitialState(l));
  const { setLanguage } = useContext(MinorContext);

  const handleClick = e => {
    let value = e.target.value;
    if (value === "ES") {
      setLanguage(0);
      setLangClass(["selected", ""]);
    } else {
      setLanguage(1);
      setLangClass(["", "selected"]);
    }
  };

  return (
    <div className="languageLabel">
      <button
        className={`lang ${langClass[0]}`}
        onClick={handleClick}
        value="ES"
      >
        ES
      </button>
      <div />
      <button
        className={`lang ${langClass[1]}`}
        onClick={handleClick}
        value="EN"
      >
        EN
      </button>
    </div>
  );
};
