import React, { useState, useEffect } from "react";
import "./App.css";
import firebase from "./firebase";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { _ILLUSTRATION, _WEBDESIGN, _ABOUT } from "./Const";
import { MinorContext } from "./MinorContext";
import Home from "./components/Home";
import Illustration from "./components/Illustration";
import WebDesign from "./components/WebDesign";
import Page from "./components/Page";
import About from "./components/About";

const HomePage = ({ match }) => {
  window.scrollTo(0, 0);
  return <Home match={match} />;
};
const IllustrationPage = ({ match }) => {
  window.scrollTo(0, 0);
  return (
    <Page match={match}>
      <Illustration />;
    </Page>
  );
};
const WebDesignPage = ({ match }) => {
  window.scrollTo(0, 0);
  return (
    <Page match={match}>
      <WebDesign match={match} />;
    </Page>
  );
};
const AboutPage = ({ match }) => {
  window.scrollTo(0, 0);
  return (
    <Page match={match}>
      <About />
    </Page>
  );
};
const Error404 = () => {
  return <div>404 Error</div>;
};

function App() {
  const [landingOn, setLandingOn] = useState(true);
  // 0 = Español, 1 = ingles
  const [language, setLanguage] = useState(1);
  const [illustrationsData, setIllustrationsData] = useState([]);

  useEffect(() => {
    firebase
      .firestore()
      .collection("illustrations")
      .onSnapshot(snap => {
        const newData = snap.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }));
        newData.sort((a, b) => {
          return b.order - a.order;
        });
        setIllustrationsData(newData);
      });
  }, []);

  return (
    <div className="App">
      <MinorContext.Provider
        value={{
          landingOn,
          setLandingOn,
          language,
          setLanguage,
          illustrationsData
        }}
      >
        <Router>
          <Switch>
            <Route path={`/${_ILLUSTRATION}/`} component={IllustrationPage} />
            <Route path={`/${_WEBDESIGN}/`} component={WebDesignPage} />
            <Route path={`/${_ABOUT}/`} component={AboutPage} />
            <Route path="/" exact component={HomePage} />
            <Route component={Error404} />
          </Switch>
        </Router>
      </MinorContext.Provider>
    </div>
  );
}

export default App;
