import React, { useContext } from "react";
import Project from "./Project";
import { Route } from "react-router-dom";
import { ProjectsWebData } from "../Data/ProjectsWeb";
import { MinorContext } from "../MinorContext";
import ProjectPage from "./ProjectPage";

const titleText = ["Diseño Web", "Web Design"];

const WebDesign = ({ match }) => {
  const { language } = useContext(MinorContext);
  return (
    <div className="WebDesign">
      <div className="container page">
        <h1 className="title">{titleText[language]}</h1>
        <Route exact path={`${match.url}/`} component={ProjectsGalery} />
        <Route path={`${match.url}/:id`} component={projectPage} />
      </div>
    </div>
  );
};

const projectPage = ({ match }) => {
  return <ProjectPage id={match.params.id - 1} />;
};

const ProjectsGalery = ({ match }) => {
  const { language } = useContext(MinorContext);

  return (
    <div className="proyectos">
      {ProjectsWebData.map((p, i) => {
        return (
          <Project
            linkto={`${match.url}${i + 1}`}
            name={p.name[language]}
            key={i}
            img={p.img}
          >
            {p.description[language]}
          </Project>
        );
      })}
    </div>
  );
};
export default WebDesign;
