import React, { useContext } from "react";
import { MinorContext } from "../MinorContext";
import "./Landing.css";
import { LandingText } from "../Data/Text";

const Landing = ({ on }) => {
  const { language } = useContext(MinorContext);
  return (
    <div className={`Landing ${on}`}>
      <div className="container">
        <div className="text">
          <h4>@dizmauri</h4>
          <h1>Diaz J. Mauricio</h1>
          <h4>{LandingText.landing[language]}</h4>
        </div>
        <div className="LandingImg" />
      </div>
      <div className="container PerfilBox">
        <div className="PerfilPicture" />
      </div>
    </div>
  );
};

export default Landing;
