import React, { useState, useContext } from "react";
import "./Illustration.css";
import { MinorContext } from "../MinorContext";
import Modal from "./Modal";

const titleText = ["Ilustración", "Illustration"];

const changeIndex = (index, val, illustrationsData) => {
  let i = index + val;
  return i > illustrationsData.length - 1
    ? 0
    : i < 0
    ? illustrationsData.length - 1
    : i;
};

const Illustration = props => {
  const [modalActive, setModalActive] = useState("");
  const [modalDataIndex, setModalDataIndex] = useState(0);
  const { language, illustrationsData } = useContext(MinorContext);

  const OpenModal = index => {
    setModalActive("active");
    setModalDataIndex(index);
  };
  const changeImgModal = val => {
    setModalDataIndex(changeIndex(modalDataIndex, val, illustrationsData));
  };

  return (
    <div className="illustration">
      {illustrationsData.length > 0 ? (
        <Modal
          active={modalActive}
          setActive={setModalActive}
          index={modalDataIndex}
          changeImgModal={changeImgModal}
        />
      ) : null}
      <div className="container page">
        <h1 className=" title">{titleText[language]}</h1>
        <Galery OpenModal={OpenModal} />
      </div>
    </div>
  );
};

export default Illustration;

const Galery = ({ OpenModal }) => {
  const { illustrationsData } = useContext(MinorContext);
  if (illustrationsData.length > 0) {
    return (
      <>
        <div className="gridBig">
          {illustrationsData.map((foto, index) => {
            if (index < 3) {
              return (
                <Foto data={foto} i={index} key={index} onClick={OpenModal} />
              );
            }
            return null;
          })}
        </div>
        {/* GRID */}
        <div className="grid">
          {illustrationsData.map((foto, index) => {
            if (index > 2) {
              return (
                <Foto data={foto} i={index} key={index} onClick={OpenModal} />
              );
            }
            return null;
          })}
        </div>
      </>
    );
  }
  return (
    <div className="loading">
      <div className="loadingIcon"></div>
      <h4>Loading . . .</h4>
    </div>
  );
};

const Foto = ({ data, onClick, i }) => {
  const fotoOnClick = val => {
    onClick(i);
  };
  return (
    <div
      className="foto"
      style={{ backgroundImage: `url(${data.url})` }}
      alt={data.name}
      onClick={fotoOnClick}
    />
  );
};

