import React, { useContext } from "react";
import "./ProjectPage.css";
import { ProjectsWebData } from "../Data/ProjectsWeb";
import Flecha from "../Icons/flecha";
import { _WEBDESIGN } from "../Const";
import { MinorContext } from "../MinorContext";
import { Link } from "react-router-dom";

const bottonsText = {
  previous: ["Anterior", "Previous"],
  back: ["Volver a Diseño Web", "Back to Web design"],
  next: ["Siguiente", "Next"]
};

const ProjectPage = ({ id }) => {
  const { language } = useContext(MinorContext);
  const getId = direction => {
    let i = id + direction;
    i =
      i > ProjectsWebData.length - 1
        ? 0
        : i < 0
        ? ProjectsWebData.length - 1
        : i;
    return i + 1;
  };
  return (
    <div className="projectPage container">
      <h2>{ProjectsWebData[id].name[language]}</h2>
      {ProjectsWebData[id].content.map((text, i) => {
        //  Imagenes
        if (text.includes("<img/>")) {
          return <ProjectImg text={text} id={id} key={i} />;
        }
        //  Videos
        else if (text.includes("<vid/>")) {
          return <ProjectVideo text={text} id={id} key={i} />;
        } else if (text.includes("<lin/>")) {
          return <ProjectLink text={text} l={language} key={i} />;
        }
        //  Textos
        else if (Array.isArray(text)) {
          return <ProjectText text={text[language]} key={i} />;
        }
        //  Default
        return <p key={i}>{text}</p>;
      })}
      {/* Selectores */}
      <div className="projectPageSelectors">
        <Link to={`/${_WEBDESIGN}/${getId(-1)}`}>
          <button className="buttonProjectPage back">
            <Flecha angle={-90} />
            {bottonsText.previous[language]}
          </button>
        </Link>

        <Link to={`/${_WEBDESIGN}/`}>
          <button className="buttonProjectPage">
            {bottonsText.back[language]}
          </button>
        </Link>
        <Link to={`/${_WEBDESIGN}/${getId(1)}`}>
          <button className="buttonProjectPage next">
            {bottonsText.next[language]}
            <Flecha angle={90} />
          </button>
        </Link>
      </div>
    </div>
  );
};

export default ProjectPage;

const ProjectImg = ({ text, id }) => {
  text = text.slice(6);
  return (
    <img src={text} samesite="None" alt={`${ProjectsWebData[id].name} ${id}`} />
  );
};
const ProjectVideo = ({ text, id }) => {
  text = text.slice(6);
  return (
    <video
      src={text}
      autoPlay
      samesite="None"
      muted
      secure="true"
      loop
      alt={`${ProjectsWebData[id].name} ${id}`}
    />
  );
};

const ProjectText = ({ text }) => {
  if (text.includes("<h4t/>")) {
    let sliceText = text.slice(6);
    return <h4>{sliceText}</h4>;
  }
  return <p>{text}</p>;
};
const ProjectLink = ({ text }) => {
  const { language } = useContext(MinorContext);
  let sliceText = text.slice(6);
  const BotonText = ["Visitar Sitio", "Visit website"];
  return (
    <a
      href={sliceText}
      target="_blank"
      rel="noopener noreferrer"
      className="link"
    >
      <button>{BotonText[language]}</button>
    </a>
  );
};
