import React, { useContext } from "react";
import "./About.css";
import Icons from "./Icons";
import { MinorContext } from "../MinorContext";
import { ContactText } from "../Data/Text";

const About = props => {
  const { language } = useContext(MinorContext);

  return (
    <div className="about">
      <div className="bgc">
        <div className="container">
          <h1 className="title">{ContactText.about[language]}</h1>
          <div className="textContainer">
            <div>
              <p className="text">{ContactText.text1[language]}</p>
              <p className="text">{ContactText.text2[language]}</p>
              <p className="text">{ContactText.text3[language]}</p>
              <p className="text">{ContactText.text4[language]}</p>
            </div>
            <div className="fotoPerfil" />
          </div>
        </div>
      </div>
      <div className="container">
        <div className="extras">
          <div className="SocialMedia">
            <h3>Social Media</h3>
            <Icons></Icons>
            <p>
              Feel free to contact me at: <b>diazjmauricio95@gmail.com</b>
            </p>
          </div>
          <div className="programas">
            <h3>Software/Languages</h3>
            <ul>
              <li>- Photoshop</li>
              <li>- Unity3D</li>
              <li>- Figma</li>
              <li>- Adobe XD</li>
              <li>- JavaScript</li>
              <li>- CSS</li>
              <li>- ReactJs</li>
              <li>- php</li>
              <li>- Java</li>
              <li>- C#</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;
