import React, { useContext } from "react";
import "./Modal.css";
import Flecha from "../Icons/flecha";
import { MinorContext } from "../MinorContext";

const Modal = ({ setActive, active, index, changeImgModal }) => {
  const handleClose = () => {
    setActive("");
  };

  return (
    <div className={`Modal ${active}`}>
      <div className="MondalContainer">
        <button className="close" onClick={handleClose}>
          <svg
            aria-hidden="true"
            focusable="false"
            data-prefix="fab"
            viewBox="0 0 340 340"
            role="img"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fill="currentColor"
              d="M0.294376 283.137L56.8629 339.706L170 226.569L283.137 339.706L339.706 283.137L226.569 170L339.706 56.8629L283.137 0.294373L170 113.431L56.8629 0.294386L0.294369 56.8629L113.431 170L0.294376 283.137Z"
            />
          </svg>
        </button>
        <ModalFoto index={index} changeImgModal={changeImgModal} />
      </div>
    </div>
  );
};

export default Modal;

const ModalFoto = ({ index, changeImgModal }) => {
  const { illustrationsData } = useContext(MinorContext);
  const handleClick = val => {
    changeImgModal(val);
  };
  return (
    <div className="container ModalFotoContainer">
      <button
        className="modalButton"
        onClick={() => {
          handleClick(-1);
        }}
      >
        <Flecha angle={-90} />
      </button>
      <div className="ModalFoto">
        <img
          src={illustrationsData[index].url}
          alt={illustrationsData[index].name}
        />
        <h3>{illustrationsData[index].name}</h3>
      </div>
      <button
        className="modalButton"
        onClick={() => {
          handleClick(1);
        }}
      >
        <Flecha angle={90} />
      </button>
    </div>
  );
};
